html, body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background: #000000;
  color: #fff;
  height: 100%;
}

.pageHollder {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px;
  align-items: center; 
  text-align: center; 
  color: #fff; 
  border: 3px solid #000000a9; 
  border-radius: 20px;
  margin: 20px;
  padding: 20px; /* Adjust the padding as needed */
  width: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
  overflow-y: hidden;
  background: #242424;      
}

.title-Box {
  overflow-x: hidden;
  overflow-y: hidden;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: center;
  width: 100%;
}

.title-bar {
  box-sizing: border-box;
  overflow-x: hidden;
  overflow-y: hidden;
  display: flex;
  flex-direction: row;
  background-color: #303030;
  align-items: center; 
  text-align: center; 
  padding: 20px;
  height: 100px;
  width: 100%; 
  border: 5px solid #202020; 
}

.title {
  overflow-x: hidden;
  overflow-y: hidden;
  color: #888;
  margin: 0;
  padding: 0;
  font-size: 24px;
}

.author {
  overflow-x: hidden;
  overflow-y: hidden;
  margin-bottom: 0;
  padding: 10;
  font-size: 14px;
  color: #888;
}

.timer {
  overflow: hidden;
  margin-bottom: 0;
  margin-left: 500px;
  font-size: 25px;
  color: #ff0000;
}

.logo {
  padding: 10px;
}

.charts-holder {
  flex: 1; 
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px;
  align-items: center; 
  text-align: center; 
  color: #fff; 
  border: 3px solid #ff3939a9; 
  border-radius: 20px;
  margin: 20px;
  padding: 20px; 
  width: 100%;
  background:#5a5a5a;
}
.CenterCharts {
  overflow-x: hidden;
  overflow-y: hidden;
  border-radius: 5px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 20px;
  width: 100%;
  min-height: 500px; /* Adjust this value as needed */
  
  
}

.chart-grid {
  box-sizing: border-box;
  display: flex;
  grid-template-columns: 1fr;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  padding: 10px;
  width: 100%;
  height: 100%;
  flex: 1; /* Allow the chart grid to expand and take up available space */
  width: 100%; /* Set width to 100% for dynamic resizing */
  height: 100%;
}

.chart-row {
  display: flex;
  flex-wrap: wrap; /* Allow elements to wrap to the next line */
  justify-content: space-evenly; /* Adjust as needed */
  gap: 20px;
  width: 100%; /* Set width to 100% for dynamic resizing */
  height: 100%;
}

.chart {
  text-align: center;
  display: 'inline-block';
  margin: '10px';
  margin-bottom: 20px;
  border-radius: 20px;
  padding: 10px;
  border: 1px solid #000000;
  background-color: #242424;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
 
}

.chart-title {
  overflow-x: hidden;
  overflow-y: hidden;
  background: #000000; 
  color: #ffffff; 
}

.chart-label {
  overflow-x: hidden;
  overflow-y: hidden;
  color: #ffffff; 
}

.player-card-holder {
  display: flex;
  flex-wrap: wrap;
  background:#5a5a5a;
  justify-content: space-evenly;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px;
  align-items: center; 
  text-align: center; 
  color: #fff; 
  border: 3px solid #000000a9; 
  border-radius: 20px;
  margin: 20px;
  padding: 20px; /* Adjust the padding as needed */
  width: 100%;
}