.blank-slot {
  text-align: center;
  display: inline-block;
  padding: 10px;
  border: 1px solid #000000;
  border-radius: 20px;
  background-color: #000000;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  width: 400px; /* Set width to 100% for dynamic resizing */
  height: 400px;
}
.blank-slot-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #4e4e4e; 
}

  